const root = document.querySelector('#root');

const wrapper = document.createElement('main');
wrapper.className = 'wrapper';

//1. ----------------------------------------------------------------------------Header---------------------------------------------------------------------------------//

// Header
const header = document.createElement('header');
header.className = 'header';

// Header Logo
const headerLogoLink = document.createElement('a');
headerLogoLink.href = "http://localhost:1234/";

import wildberries from './assets/img/Wildberries.svg';

const headerLogo = document.createElement('img');
headerLogo.className = 'header__logo';
headerLogo.type = 'button';
headerLogo.src = wildberries;
headerLogo.alt = 'Wildberries';

// Header Input
const headerInput = document.createElement('input');
headerInput.className = 'header__input';
headerInput.type = 'text';
headerInput.placeholder = 'Find on Wildberries';
headerInput.value = '';

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è input –¥–ª—è —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏ –∫–∞—Ä—Ç–æ—á–µ–∫ –ø–æ –≤–≤–µ–¥–µ–Ω–Ω–æ–º—É —Ç–µ–∫—Å—Ç—É
headerInput.addEventListener('input', (event) => {
    const searchValue = event.target.value.toLowerCase(); // –ü–æ–ª—É—á–∞–µ–º –≤–≤–µ–¥–µ–Ω–Ω—ã–π —Ç–µ–∫—Å—Ç –∏ –ø—Ä–∏–≤–æ–¥–∏–º –∫ –Ω–∏–∂–Ω–µ–º—É —Ä–µ–≥–∏—Å—Ç—Ä—É
    const shoppingCards = document.querySelectorAll('.shopping__card'); // –í—ã–±–∏—Ä–∞–µ–º –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏

    // –ü—Ä–æ—Ö–æ–¥–∏–º—Å—è –ø–æ –∫–∞–∂–¥–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ –∏ —Å–∫—Ä—ã–≤–∞–µ–º —Ç–µ, –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—Ç –≤–≤–µ–¥–µ–Ω–Ω–æ–º—É —Ç–µ–∫—Å—Ç—É
    shoppingCards.forEach(card => {
        const cardName = card.querySelector('.shopping__card-name').textContent.toLowerCase(); // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—Å—Ç–æ–≤–æ–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –∏ –ø—Ä–∏–≤–æ–¥–∏–º –∫ –Ω–∏–∂–Ω–µ–º—É —Ä–µ–≥–∏—Å—Ç—Ä—É
        if (cardName.includes(searchValue)) {
            card.style.display = 'block'; // –ï—Å–ª–∏ —Ç–µ–∫—Å—Ç –∫–∞—Ä—Ç–æ—á–∫–∏ —Å–æ–¥–µ—Ä–∂–∏—Ç –≤–≤–µ–¥–µ–Ω–Ω—ã–π —Ç–µ–∫—Å—Ç, –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É
        } else {
            card.style.display = 'none'; // –ò–Ω–∞—á–µ —Å–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É
        }
    });
});

import loupe from './assets/img/loupe-svg.svg';

const loupeImageInInput = document.createElement('img');
loupeImageInInput.className = 'loupe__image';
loupeImageInInput.src = loupe;
loupeImageInInput.alt = 'Loupe';

// Header Basket
const basketContainer = document.createElement('div');
basketContainer.className = 'header__basket';

import basket from './assets/img/basket.svg';

const basketImage = document.createElement('img');
basketImage.className = 'header__basket-image';
basketImage.src = basket;
basketImage.alt = 'Basket';

const basketText = document.createElement('span');
basketText.className = 'header__basket-text';
basketText.textContent = '–ö–æ—Ä–∑–∏–Ω–∞';

// –°–æ–∑–¥–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –¥–ª—è —Å—á–µ—Ç—á–∏–∫–∞ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
const basketCounter = document.createElement('div');
basketCounter.className = 'header__basket-counter';
basketCounter.textContent = '0';
//------------------------------------------------------------------------------Header---------------------------------------------------------------------------------//

//2. ---------------------------------------------------------------------------Slider---------------------------------------------------------------------------------//

import sliderImage1 from './assets/img/slider.webp';
import sliderImage2 from './assets/img/slider2.webp';
import sliderImage3 from './assets/img/slider3.webp';

// –°–æ–∑–¥–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Å–ª–∞–π–¥–µ—Ä–∞
const sliderContainer = document.createElement('div');
sliderContainer.id = 'slider';
sliderContainer.className = 'slider';

// –î–∞–Ω–Ω—ã–µ –æ —Å–ª–∞–π–¥–∞—Ö
const slidesData = [
    { content: sliderImage1 },
    { content: sliderImage2 }, 
    { content: sliderImage3 }  
];

// –°–æ–∑–¥–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Å–ª–∞–π–¥–æ–≤
const slidesContainer = document.createElement('div');
slidesContainer.id = 'slides';
slidesContainer.className = 'slider__slides';

// –°–æ–∑–¥–∞–µ–º –∫–Ω–æ–ø–∫–∏ "–Ω–∞–∑–∞–¥" –∏ "–≤–ø–µ—Ä–µ–¥"
const prevButton = document.createElement('button');
prevButton.id = 'prev';
prevButton.className = 'slider__prev';
prevButton.textContent = '‚Äπ';

const nextButton = document.createElement('button');
nextButton.id = 'next';
nextButton.className = 'slider__next';
nextButton.textContent = '‚Ä∫';

// –°–æ–∑–¥–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ç–æ—á–µ–∫ (–∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–æ–≤ —Å–ª–∞–π–¥–æ–≤)
const dotsContainer = document.createElement('div');
dotsContainer.id = 'dots';
dotsContainer.className = 'slider__dots';

// –ü—Ä–æ—Ö–æ–¥–∏–º—Å—è –ø–æ –∫–∞–∂–¥–æ–º—É —Å–ª–∞–π–¥—É –∏ –¥–æ–±–∞–≤–ª—è–µ–º –µ–≥–æ –≤ —Å–ª–∞–π–¥–µ—Ä
slidesData.forEach((slide, index) => {
    const slideElement = document.createElement('div');
    slideElement.className = 'slide';

    // –ï—Å–ª–∏ –∫–æ–Ω—Ç–µ–Ω—Ç —Å–ª–∞–π–¥–∞ - –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ, —Å–æ–∑–¥–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç img –∏ –¥–æ–±–∞–≤–ª—è–µ–º –µ–≥–æ –≤ —Å–ª–∞–π–¥
    if (slide.content.includes('.jpg') || slide.content.includes('.png') || slide.content.includes('.webp')) {
        const image = document.createElement('img');
        image.src = slide.content;
        image.alt = 'Slide';
        slideElement.appendChild(image);
    } else {
        slideElement.textContent = slide.content; // –ò–Ω–∞—á–µ –¥–æ–±–∞–≤–ª—è–µ–º —Ç–µ–∫—Å—Ç–æ–≤—ã–π –∫–æ–Ω—Ç–µ–Ω—Ç –≤ —Å–ª–∞–π–¥
    }

    slidesContainer.appendChild(slideElement); // –î–æ–±–∞–≤–ª—è–µ–º —Å–ª–∞–π–¥ –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å–ª–∞–π–¥–æ–≤

    // –°–æ–∑–¥–∞–µ–º —Ç–æ—á–∫—É (–∏–Ω–¥–∏–∫–∞—Ç–æ—Ä) –¥–ª—è —ç—Ç–æ–≥–æ —Å–ª–∞–π–¥–∞
    const dot = document.createElement('span');
    dot.className = 'dot';
    dot.dataset.slideIndex = index; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∏–Ω–¥–µ–∫—Å —Å–ª–∞–π–¥–∞ –¥–ª—è –¥–∞–ª—å–Ω–µ–π—à–µ–≥–æ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è
    dotsContainer.appendChild(dot); // –î–æ–±–∞–≤–ª—è–µ–º —Ç–æ—á–∫—É –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä —Ç–æ—á–µ–∫
});

let currentIndex = 0; // –ò–Ω–¥–µ–∫—Å —Ç–µ–∫—É—â–µ–≥–æ —Å–ª–∞–π–¥–∞ (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é - –ø–µ—Ä–≤—ã–π)

// –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π –¥–ª—è –∫–Ω–æ–ø–æ–∫ "–Ω–∞–∑–∞–¥" –∏ "–≤–ø–µ—Ä–µ–¥"
prevButton.addEventListener('click', () => {
    currentIndex = (currentIndex - 1 + slidesData.length) % slidesData.length;
    updateSlide(); // –û–±–Ω–æ–≤–ª—è–µ–º –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤
});

nextButton.addEventListener('click', () => {
    currentIndex = (currentIndex + 1) % slidesData.length;
    updateSlide(); // –û–±–Ω–æ–≤–ª—è–µ–º –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤
});

// –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –¥–ª—è —Ç–æ—á–µ–∫ (–∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–æ–≤ —Å–ª–∞–π–¥–æ–≤)
dotsContainer.addEventListener('click', event => {
    const dot = event.target.closest('.dot');
    if (dot) {
        currentIndex = parseInt(dot.dataset.slideIndex);
        updateSlide(); // –û–±–Ω–æ–≤–ª—è–µ–º –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤
    }
});

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–≥–æ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è —Å–ª–∞–π–¥–æ–≤
const startSlideShow = () => {
    setInterval(() => {
        currentIndex = (currentIndex + 1) % slidesData.length;
        updateSlide();
    }, 3000);
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —Å–ª–∞–π–¥–æ–≤
const updateSlide = () => {
    const slides = document.querySelectorAll('.slide'); // –í—ã–±–∏—Ä–∞–µ–º –≤—Å–µ —Å–ª–∞–π–¥—ã
    const dots = document.querySelectorAll('.dot'); // –í—ã–±–∏—Ä–∞–µ–º –≤—Å–µ —Ç–æ—á–∫–∏

    // –ü—Ä–æ—Ö–æ–¥–∏–º—Å—è –ø–æ –∫–∞–∂–¥–æ–º—É —Å–ª–∞–π–¥—É –∏ —Å–∫—Ä—ã–≤–∞–µ–º –∏–ª–∏ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–≥–æ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–µ–∫—É—â–µ–≥–æ –∏–Ω–¥–µ–∫—Å–∞
    slides.forEach((slide, index) => {
        slide.style.display = index === currentIndex ? 'block' : 'none';
    });

    // –ü–æ–¥—Å–≤–µ—á–∏–≤–∞–µ–º —Ç–µ–∫—É—â—É—é —Ç–æ—á–∫—É, —É–±–∏—Ä–∞—è –ø–æ–¥—Å–≤–µ—Ç–∫—É —É –æ—Å—Ç–∞–ª—å–Ω—ã—Ö
    dots.forEach((dot, index) => {
        dot.classList.toggle('active', index === currentIndex);
    });
};

document.addEventListener("DOMContentLoaded", function() {
    updateSlide(); // –í—ã–∑—ã–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é updateSlide() –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    startSlideShow(); // –ó–∞–ø—É—Å–∫–∞–µ–º –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–µ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤
});

// –î–æ–±–∞–≤–ª—è–µ–º —Å—Ç—Ä–µ–ª–∫–∏ –∏ —Ç–æ—á–∫–∏ –≤–Ω—É—Ç—Ä—å —Å–ª–∞–π–¥–µ—Ä–∞
sliderContainer.append(prevButton, slidesContainer, nextButton, dotsContainer);

//--------------------------------------------------------------------------------Slider---------------------------------------------------------------------------------//

// 3. ----------------------------------------------------------------------------Shopping Cards------------------------------------------------------------------------ //
const allShoppingCards = document.createElement('div');
allShoppingCards.className = 'shopping__cards';

// –û–±—ä—è–≤–ª—è–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é totalPrice –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –æ–±—â–µ–π —Å—É–º–º—ã –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
 let totalPrice = 0;

// –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
fetch('https://65ca55fb3b05d29307e02af2.mockapi.io/users')
    .then(response => response.json())
    .then(data => {
        // –°–æ–∑–¥–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–∞ –æ—Å–Ω–æ–≤–µ –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
        data.forEach(item => {
            const shoppingCard = document.createElement('div');
            shoppingCard.className = 'shopping__card';

            // –°–æ–∑–¥–∞–µ–º –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ
            const image = document.createElement('img');
            image.className = 'shopping__card-image'
            image.src = item.image + '?random=' + Math.random();
            image.alt = 'Product Image';

            // –°–æ–∑–¥–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ —Ç–æ–≤–∞—Ä–µ
            const price = document.createElement('h3');
            price.className = 'shopping__card-price'
            price.textContent = `${item.price} —Ä.`;

            const date = document.createElement('p');
            date.className = 'shopping__card-date'
            date.textContent = `–ü–æ—Å–ª–µ–∑–∞–≤—Ç—Ä–∞`;

            const name = document.createElement('p');
            name.className = 'shopping__card-name'
            name.textContent = item.name;

            const rate = document.createElement('p');
            rate.className = 'shopping__card-rate'
            rate.textContent = `‚òÖ ${item.rate}`;

            const reviews = document.createElement('p');
            reviews.className = 'shopping__card-reviews'
            reviews.textContent = `${item.reviews} –æ—Ç–∑—ã–≤–æ–≤`;

            // –°–æ–∑–¥–∞–µ–º –∫–Ω–æ–ø–∫—É "–í –∫–æ—Ä–∑–∏–Ω—É"
            const shoppingCardAddBtn = document.createElement('input');
            shoppingCardAddBtn.className = 'shopping__card-addbtn';
            shoppingCardAddBtn.type = 'button';
            shoppingCardAddBtn.value = '–í –∫–æ—Ä–∑–∏–Ω—É';

            // –î–æ–±–∞–≤–ª—è–µ–º –≤—Å–µ —Å–æ–∑–¥–∞–Ω–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã –≤ –∫–∞—Ä—Ç–æ—á–∫—É
            shoppingCard.appendChild(image);
            shoppingCard.appendChild(price);
            shoppingCard.appendChild(date);
            shoppingCard.appendChild(name);
            shoppingCard.appendChild(rate);
            shoppingCard.appendChild(reviews);
            shoppingCard.appendChild(shoppingCardAddBtn);

            // –î–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –≤—Å–µ—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
            allShoppingCards.appendChild(shoppingCard);

            // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –Ω–∞ –∫–Ω–æ–ø–∫—É "–í –∫–æ—Ä–∑–∏–Ω—É"
            shoppingCardAddBtn.addEventListener('click', () => {
                const cardItemInBasket = document.createElement('div');
                cardItemInBasket.className = 'card__item';

                const itemName = document.createElement('div');
                itemName.className = 'modal__window-item';
                itemName.textContent = item.name;

                const itemPrice = document.createElement('div');
                itemPrice.className = 'modal__window-price';
                itemPrice.textContent = `${item.price} —Ä.`;

                cardItemInBasket.appendChild(itemName);
                cardItemInBasket.appendChild(itemPrice);

                // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ —Å—á–µ—Ç—á–∏–∫–∞ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ –ø—Ä–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –Ω–æ–≤–æ–≥–æ —Ç–æ–≤–∞—Ä–∞
                let count = parseInt(basketCounter.textContent);
                count++;
                basketCounter.textContent = count; // –û–±–Ω–æ–≤–ª—è–µ–º —Ç–µ–∫—Å—Ç —Å—á–µ—Ç—á–∏–∫–∞

                // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å–ª–∏ —Å—á–µ—Ç—á–∏–∫ —Ä–∞–≤–µ–Ω 0, —Ç–æ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–≥–æ, –∏–Ω–∞—á–µ —Å–∫—Ä—ã–≤–∞–µ–º
                basketCounter.style.display = count === 0 ? 'none' : 'block';

                modalWindow.appendChild(cardItemInBasket);

                // –û–±–Ω–æ–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ —Å—É–º–º—ã —Ü–µ–Ω –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
                totalPrice += parseFloat(item.price);

                // –°–æ–∑–¥–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –æ–±—â–µ–π —Å—É–º–º—ã —Ü–µ–Ω –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
                const itemPriceAll = document.querySelector('.modal__window-priceall');
                if (itemPriceAll) {
                    itemPriceAll.textContent = `–ò—Ç–æ–≥–æ: ${totalPrice.toFixed(2)} —Ä.`;
                } else {
                    const newItemPriceAll = document.createElement('div');
                    newItemPriceAll.textContent = `–ò—Ç–æ–≥–æ: ${totalPrice.toFixed(2)} —Ä.`;
                    newItemPriceAll.className = 'modal__window-priceall';
                    modalWindow.appendChild(newItemPriceAll);
                }
            });
        });
    })
    .catch(error => {
        console.error('Error fetching data:', error);
    });
//-------------------------------------------------------------------------------Shopping Cards------------------------------------------------------------------------ //

// 4. ----------------------------------------------------------------------------Basket Model Window------------------------------------------------------------------- //
const modalWindow = document.createElement('div')
modalWindow.className = 'modal__window'

const modalWindowHeader = document.createElement('header')
modalWindowHeader.className = 'modal__window_header'

const modalWindowHeaderTitle = document.createElement('h1')
modalWindowHeaderTitle.className = 'modal__window_header-title'
modalWindowHeaderTitle.innerText = '–ö–æ—Ä–∑–∏–Ω–∞'

const modalWindowHeaderBtn = document.createElement('input')
modalWindowHeaderBtn.className = 'modal__window_header-btn'
modalWindowHeaderBtn.type = 'button'
modalWindowHeaderBtn.value = '–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É'

// –î–æ–±–∞–≤–ª—è–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
let isModalOpen = false;

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –Ω–∞ –∫–æ—Ä–∑–∏–Ω—É
basketContainer.addEventListener('click', (event) => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –æ—Ç–∫—Ä—ã—Ç–æ –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    if (!isModalOpen) {
        modalWindow.style.display = 'block';
        isModalOpen = true; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ñ–ª–∞–≥, —á—Ç–æ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –æ—Ç–∫—Ä—ã—Ç–æ
    }
    // –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º —Ä–∞—Å–ø—Ä–æ—Å—Ç—Ä–∞–Ω–µ–Ω–∏–µ —Å–æ–±—ã—Ç–∏—è, —á—Ç–æ–±—ã –æ–Ω–æ –Ω–µ –¥–æ—à–ª–æ –¥–æ document.addEventListener
    event.stopPropagation();
});

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –≤–Ω–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
document.addEventListener('click', (event) => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –±—ã–ª –ª–∏ –∫–ª–∏–∫ —Å–¥–µ–ª–∞–Ω –≤–Ω–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    if (!modalWindow.contains(event.target) && event.target !== basketContainer) {
        modalWindow.style.display = 'none'; // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
        isModalOpen = false; // –°–±—Ä–∞—Å—ã–≤–∞–µ–º —Ñ–ª–∞–≥ —Å–æ—Å—Ç–æ—è–Ω–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    }
});

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –Ω–∞ –∫–Ω–æ–ø–∫—É "–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É"
modalWindowHeaderBtn.addEventListener('click', () => {
    // –£–¥–∞–ª—è–µ–º —Ç–æ–ª—å–∫–æ —ç–ª–µ–º–µ–Ω—Ç—ã, –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è—é—â–∏–µ —Ç–æ–≤–∞—Ä—ã –≤ –∫–æ—Ä–∑–∏–Ω–µ
    const cardItems = modalWindow.querySelectorAll('.card__item');
    cardItems.forEach(cardItem => {
        modalWindow.removeChild(cardItem);
    });
    const itemPriceAll = modalWindow.querySelectorAll('.modal__window-priceall');
    itemPriceAll.forEach(itemPriceAll => {
        modalWindow.removeChild(itemPriceAll);
    });

    // –û–±–Ω–æ–≤–ª—è–µ–º —Å—á–µ—Ç—á–∏–∫ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
    basketCounter.textContent = '0';
    if (parseInt(basketCounter.textContent) === 0) {
        basketCounter.style.display = 'none';
    } else {
        basketCounter.style.display = 'block';
    }
});
// --------------------------------------------------------------------------------Basket Model Window------------------------------------------------------------------ //

root.append(wrapper);
wrapper.append(header, sliderContainer, allShoppingCards, modalWindow);
header.append(headerLogoLink, headerInput, basketContainer);

headerLogoLink.appendChild(headerLogo);
headerInput.appendChild(loupeImageInInput);

basketContainer.appendChild(basketImage);
basketContainer.appendChild(basketText);
basketContainer.appendChild(basketCounter);

modalWindow.appendChild(modalWindowHeader)
modalWindowHeader.appendChild(modalWindowHeaderTitle)
modalWindowHeader.appendChild(modalWindowHeaderBtn)




